import React from 'react';
import Navbar from '../../components/Navbar/Navbar';
import DCarousel from '../../components/carousel/carousel';
import About from '../../components/About/About';
import Courses from '../../components/Courses/courses';
import Staff from '../../components/Staff/staff';
import StudentResult from '../../components/Student Result/studentResult';
import StudentFeedback from '../../components/Student Feedback/studentFeedback';
import Footer from '../../components/Footer/footer';
import CourseData from '../../components/Courses/coursesData';
import Tuitions from '../../components/Tuitions/index';
import TuitionData from '../../components/Tuitions/tuitionData';

const Home = () => {
  return (
    <div className="app">
      <Navbar/>
      <DCarousel/>
      <About/>
      <div className="courses_section" id="courses">
            <div className="utility_center_text">
                   <h2 className="about_section__heading utility__bottom-border">
                      Featured courses
                   </h2>
            </div> 
            <div className="courses__card--container">
      {
        CourseData.map(course => {
          return (
            <Courses
              number={course.key}
              img={course.img}
              name={course.name}
              descrip={course.descrip}
              price={course.price}
              button={course.button}
            />
          )
        })
      }
      </div>
      </div>
      <div className="tuition-section">
      <div className="tuitions-heading">
        <h2 className="about_section__heading tuition-section--heading">
          Individual tuitions
        </h2>
      </div>
      {
        TuitionData.map(tuition => {
          return (
            <Tuitions
              number = {tuition.key}
              name = {tuition.name}
              title = {tuition.title}
              price = {tuition.price}
            />
          )
        })
      }
      </div> 
      <Staff/>
      <StudentResult/>
      <StudentFeedback/>
      <Footer/>
    </div>
  ); 
}

export default Home;