import React,{useState}from "react";
import "./style.css";
import Form from '../../components/Registration from/form';
import CloseIcon from '../../media/icons/close.svg';

const Tuitions = (props) => {
 const [show,setShow] = useState(false);

  return (
    <>
      <div className={`tuition-section-one color-${props.number%2 == 0?'two':'one'}`}>
        <h1> {props.title}</h1>
        <button className={`tuition-section--button color-${props.number%2 == 0?'one':'two'}`}
           onClick={() => setShow(true)}
        >BUY</button>
      </div>
      {show?(
        <section className="project_detail_section modal">
        <div className="project__detail modal-main">
            <button className="cancel-button" onClick={() => setShow(false)}> 
               <img style={{width:"60%"}} src={CloseIcon}></img>
            </button>
              <Form course={props}/>
          </div> 
        </section>
      ):null
      }
    </>
  );
};

export default Tuitions;
