import React,{useState} from 'react';
import './courseDetail.css';

const CourseDetail = (props) => {
  
    return (
    <>
         
        <div className="form_section" id="register">
          <div className="form_row">
            <div className="book">
              <div className="book_form">
                <div className="content">
                  <h1 className="heading-h1 utility_space">MKBang Classes</h1>
                  <h2 className="utility_space heading-h2">MKB JUNIOR COMMERCE</h2>
                  <p className="content_para">FOR CLASS 11TH AND 12TH AN INTEGRATED PROGRAM</p>
                  <p className="content_para">Both college and tuition at one place</p>
                  <p className="content_para">How MKB junior commerce better</p> 
                </div>
                <div className="table">
                   <div className="table-left">
                       <h2 className="table_title">Existing School(college) system</h2>
                       <h3 className="table_time">Teaching Time</h3>
                       <ul>
                           <li>At college:- 7:00 am - 1:00 pm = 6 Hours </li>
                           <li>At coaching:- 2:00 am - 5:00 pm = 3 Hours </li>
                           <li>Travelling Time = 2 Hours </li>
                           <li>Both are at different places </li>
                           <li>No expert faculties at one location</li>
                       </ul>
                       <h3 className="table_time">DOUBLE FEES (Expensive education)</h3>
                   </div>
                   <div className="table-right">
                       <h2 className="table_title">MKB Junior commerce system</h2>
                       <h3 className="table_time">Teaching Time</h3>
                       <ul>
                           <li>At MKB Junior commerce:- 7:00 am - 1:00 pm = 6 Hours </li>
                           <li>Travelling Time = 30 mins </li>
                           <li>Everything at one place</li>
                           <li>Expert faculties at one location</li>
                           <li>COURSES: CA, CMA, CS along with class XI AND XII</li>
                       </ul>
                       <h3 className="table_time" >SINGLE FEES (Affordable education)</h3>
                   </div>
                </div>
                <div className="button_div newbatch-button">
                <div className="new-batch-course">
                      <h1 className="new-batch">new batch starts from 13 june 2021</h1>
                    </div>
                <button
                    type="submit"
                    className="form_button btn"
                    onClick={() => props.setDetailRead(true)}
                  >
                    Next
                    <img
                      style={{ width: "20%", marginLeft: "10px" }}
                      src="https://media.giphy.com/media/ggiIT76o0nKxBq671w/giphy.gif"
                    ></img>
                  </button>
                  
                </div>
              </div>
            </div>
          </div>
      </div>
    </>
  );
};

export default CourseDetail;
