import "./courseDetail.css";
import comingSoon from '../../media/icons/soon.svg';

const CourseDetail2 = (props) => {
  return (
    <div className="form_section" id="register">
      <div className="form_row">
        <div className="book">
          <div className="book_form">
            <div className="content">
              <h1 className="heading-h1 utility_space">MKBang Classes</h1>
              <h2 className="utility_space heading-h2">{props.title}</h2>
              <p className="content_para">{props.descrip}</p>
              <h2 className="table_title">{props.overview}</h2>
              <div className="courseDetail2-container">
                <div className="courseDetail2-img--div">
                  <img
                    className="courseDetail2-img"
                    src={props.img}
                    alt="course_img"
                  ></img>
                </div>
                {
                  props.listOne?(
                    <div className="ul-courseDetail2">
                    <ul>
                    <li
                      className={`${
                        props.listOne ? null : "list-item--utility"
                      } list-item`}
                    >
                      {props.listOne}
                    </li>
                    <li
                      className={`${
                        props.listOne ? null : "list-item--utility"
                      } list-item`}
                    >
                      {props.listTwo}
                    </li>
                    <li
                      className={`${
                        props.listOne ? null : "list-item--utility"
                      } list-item`}
                    >
                      {props.listThree}
                    </li>
                    <li
                      className={`${
                        props.listOne ? null : "list-item--utility"
                      } list-item`}
                    >
                      {props.listFour}
                    </li>
                    <li
                      className={`${
                        props.listOne ? null : "list-item--utility"
                      } list-item`}
                    >
                      {props.listFive}
                    </li>
                  </ul>
                  </div>
                  ):(
                    <div className="courseDetail2-img--div coming-soon-div">
                  <img className="coming-soon--img" src={comingSoon} alt="coming_soon"></img>
                </div>
                  )
                }
                
              </div>
              {
                props.button == 'Next'?(
                  <div className="button_div">
                <button
                  type="submit"
                  className="form_button btn"
                  onClick={() => props.setDetailRead(true)}
                >
                  {props.button}
                  <img
                    style={{ width: "20%", marginLeft: "10px" }}
                    src="https://media.giphy.com/media/ggiIT76o0nKxBq671w/giphy.gif"
                  ></img>
                </button>
              </div>
                ):(
                  props.button?(
                    <div className="new-batch--div">
                      <h1 className="new-batch">{props.button}</h1>
                    </div>
                  ):null
                )
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};
export default CourseDetail2;
