import React from "react";
import "./About.css";
import about1 from "../../media/about1.png";
import about2 from "../../media/about2.jpg";
import about3 from "../../media/about31.jpg";

const About = () => {
  return (
    <section className="about_section" id="about">
      <div className="utility_center_text">
        <h2 className="about_section__heading utility_bottom_margin">
          About Us
        </h2>
        <div className="about_section__row">
          <div className="about_section__col_one">
            <p className="about_section__paragraph">
              <div className="about_section_tagline">
                <h1 className="about_section_tagline-text about_section_tagline-think-text">
                  Think
                </h1>
                <h1 className="about_section_tagline-text about_section_tagline-design-text">
                  design
                </h1>
                <h1 className="about_section_tagline-text about_section_tagline-develop-text">
                  develop
                </h1>
              </div>
              At MKBANG Classes, we are proud to offer top quality training
              courses to the people in the local area. What's our secret? We
              take great pains to simplify subjects and have a reputation for
              making difficult concepts or tasks much easier. If you don't
              believe that, rest assured that many of our students have seen
              great progress in their skills and knowledge over a very short
              period of time of time.
            </p>
          </div>
          <div class="about_section__col_two">
            <div className="composition">
              <img
                src={about3}
                alt="develop"
                className="composition_photo composition_photo_p1"
              ></img>
              <img
                src={about2}
                alt="design"
                className="composition_photo composition_photo_p2"
              ></img>
              <img
                src={about1}
                alt="think"
                className="composition_photo composition_photo_p3"
              ></img>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};
export default About;
