import React, { useState } from "react";
import "./courses.css";
import CloseIcon from "../../media/icons/close.svg";
import Form from "../../components/Registration from/form";
import CourseDetail from "../../components/courseDetail/courseDetail";
import CourseDetail2 from "../../components/courseDetail/courseDetail2";
import courseDetail2Data from "../../components/courseDetail/courseDetail2Data";

const Courses = (props) => {
  const [buy, setBuy] = useState(false);
  const [detailRead, setDetailRead] = useState(false);
  return (
    <>
      <div
        className="courses_card"
        onClick={ () => setBuy(true)}
      >
        <img src={props.img} alt="course-image"></img>
        <hr className="course_card_hr" />
        <div className="course_text">
          <h3 className="course_name"> {props.name} </h3>
          <p className="course_descrip"> {props.descrip} </p>
          <p className="course_price"> {props.price} </p>
        </div>
      </div>
      {buy ? (
        <section className="project_detail_section modal">
          <div className="project__detail modal-main">
            <button className="cancel-button" onClick={() => setBuy(false)}>
              <img
                style={{ width: "60%" }}
                src={CloseIcon}
                onClick={() => setDetailRead(false)}
              ></img>
            </button>
            {detailRead ? (
              <Form course={props} setDetailRead={setDetailRead} />
            ) : props.price.includes("14500") || props.price.includes("30000")? (
              <CourseDetail setDetailRead={setDetailRead} />
            ) : (
              <CourseDetail2
                key={courseDetail2Data[props.number].key}
                setDetailRead={setDetailRead}
                img={props.img}
                title={courseDetail2Data[props.number].courseTitle}
                descrip={courseDetail2Data[props.number].courseDescrip}
                overview={courseDetail2Data[props.number].overviewTitle}
                listOne={courseDetail2Data[props.number].listOne}
                listTwo={courseDetail2Data[props.number].listTwo}
                listThree={courseDetail2Data[props.number].listThree}
                listFour={courseDetail2Data[props.number].listFour}
                listFive={courseDetail2Data[props.number].listFive}
                button={courseDetail2Data[props.number].button}
              />
            )}
          </div>
        </section>
      ) : null}
    </>
  );
};

export default Courses;
